import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard, checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";
import { CusPoolHttpService } from "app/pages/tzb/custom/http/custom-pool/custom-pool.http.service";
import { ConfigPropertiesDetailBean } from "app/pages/tzb/custom/life-cycle/config-properties/config-properties-detail/bean/config-properties-detail.bean";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'config-properties-detail',
    templateUrl: './config-properties-detail.component.html',
    styleUrls: ['./config-properties-detail.component.scss'],
})
export class ConfigPropertiesDetailComponent implements OnInit {
    @Input('in-value') inValue: any;
    @Output() outValue = new EventEmitter();
    bean: ConfigPropertiesDetailBean = new ConfigPropertiesDetailBean();
    dataType: any = [];
    status: any = [];
    showType: any = [];
    msgs: Message[] = [];
    code: any;
    isClick: boolean = false;
    tableList2: any = [];
    isSelect: boolean = false;
    tableMesg: any = tableMessage;
    constructor(
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeValues()
        this.dataType = this.code['PoolDataType'];
        this.status = this.code['PoolStatus'];
        this.showType = this.code['PoolShowType'];
        // this.dataType = [
        //     { label: '', value: '' },
        //     { label: '', value: 's' },
        //     { label: '', value: 'i' },
        //     { label: '', value: 'f' },
        //     { label: '', value: 'dt' },
        // ]
        // this.status = [
        //     { label: '', value: '' },
        //     { label: '', value: '0' },
        //     { label: '', value: '1' },
        // ]
        // this.showType = [
        //     { label: '', value: '' },
        //     { label: '', value: '01' },
        //     { label: '', value: '04' },
        // ]
    }
    ngOnInit() {
        if (this.inValue) {
            for (let item in this.inValue) {
                this.bean[item] = this.inValue[item]
            }
        }

        if (this.bean.showType == '04') {
            this.isSelect = true;
            this.queryCustPoolConfig();
        } else {
            this.isSelect = false;
        }

    }

    // 
    queryCustPoolConfig() {
        let param = {
            inputParams: {
                indicatorId: this.bean.indicatorId,
            }
        }
        this.cusPoolHttpService.queryIndicatorEnum(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList2 = data.outputParams.indicatorList == null ? [] : data.outputParams.indicatorList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

    // /
    doSave() {
        if (!this.bean.indicatorEnName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.indicatorName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.dataType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.dataLength) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.showType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        this.bean.operType = '1';
        this.isClick = true;
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.modifyCustPoolConfig(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.isClick = false;
                this.outValue.emit(false)
            } else {
                this.isClick = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.isClick = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    // 
    return() {
        this.outValue.emit(false)
    }
}