import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard, checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";
import { ConfigPropertiesAddBean } from "app/pages/tzb/custom/life-cycle/config-properties/config-properties-add/bean/config-properties-add.bean";
import { CusPoolHttpService } from "app/pages/tzb/custom/http/custom-pool/custom-pool.http.service";
import { ConfigPropertiesUpdateBean } from "app/pages/tzb/custom/life-cycle/config-properties/config-properties-update/bean/config-properties-update.bean";
import { tableMessage } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';

@Component({
    selector: 'config-properties-update',
    templateUrl: './config-properties-update.component.html',
    styleUrls: ['./config-properties-update.component.scss'],
})
export class ConfigPropertiesUpdateComponent implements OnInit {

    @Input('in-value') inValue: any;
    @Output() outValue = new EventEmitter();
    bean: ConfigPropertiesUpdateBean = new ConfigPropertiesUpdateBean();
    dataType: any = [];
    status: any = [];
    showType: any = [];
    msgs: Message[] = [];
    code: any;
    isClick: boolean = false;
    tableMesg: any = tableMessage;
    tableList2: any = [];
    vlaue: string = '';
    oldType: string;

    constructor(
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeValues()
        this.dataType = this.code['PoolDataType'];
        this.status = this.code['PoolStatus'];
        this.showType = this.code['PoolShowType'];
        // this.dataType = [
        //     { label: '', value: '' },
        //     { label: '', value: 's' },
        //     { label: '', value: 'i' },
        //     { label: '', value: 'f' },
        //     { label: '', value: 'dt' },
        // ]
        // this.status = [
        //     { label: '', value: '' },
        //     { label: '', value: '0' },
        //     { label: '', value: '1' },
        // ]
        // this.showType = [
        //     { label: '', value: '' },
        //     { label: '', value: '01' },
        //     { label: '', value: '04' },
        // ]
    }
    ngOnInit() {
        if (this.inValue) {
            for (let item in this.inValue) {
                this.bean[item] = this.inValue[item]
            }
        }
        if (this.bean.showType == '04') {
            this.vlaue = '1';
            this.oldType = '04'
            this.queryCustPoolConfig();
        } else {
            this.oldType = '01'
            this.vlaue = '';
        }
    }

    // 
    queryCustPoolConfig() {
        let param = {
            inputParams: {
                indicatorId: this.bean.indicatorId,
            }
        }
        this.cusPoolHttpService.queryIndicatorEnum(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList2 = data.outputParams.indicatorList == null ? [] : data.outputParams.indicatorList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

    // /
    doSave() {
        if (!this.bean.indicatorEnName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.indicatorName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.dataType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.dataLength) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (!this.bean.showType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (this.bean.showType == '04' && this.tableList2.length > 0) {
            let aa = false;
            let bb = false;
            for (var i = 0; i < this.tableList2.length; i++) {
                if (!this.tableList2[i].indicatorAttrValue) {
                    aa = true;
                    break
                }
                if (!this.tableList2[i].description) {
                    bb = true
                    break
                }
            }
            if (aa) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                return
            }
            if (bb) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                return
            }
        }
        this.bean.operType = '2';
        this.isClick = true;
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.modifyCustPoolConfig(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 
                if (this.oldType == '01') {
                    if (this.bean.showType == '04' && this.tableList2.length > 0) {// -
                        let arr = [];
                        this.tableList2.forEach(element => {
                            arr.push({ indicatorId: this.bean.indicatorId, indicatorAttr: this.bean.indicatorEnName, indicatorAttrValue: element.indicatorAttrValue, description: element.description })
                        });
                        let param = {
                            inputParams: {
                                operType: '1',
                                indicatorList: arr,
                            }
                        }
                        this.cusPoolHttpService.modifyIndicatorEnum(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.isClick = false;
                                this.outValue.emit(false)
                            } else {
                                this.isClick = false;
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.isClick = false;
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: "" });
                        });
                    } else {
                        this.isClick = false;
                        this.outValue.emit(false)
                    }
                } else {
                    let param = {
                        inputParams: {
                            indicatorId: this.bean.indicatorId,
                        }
                    }
                    this.cusPoolHttpService.deleteIndicatorEnum(param).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            if (this.bean.showType == '01') {
                                this.isClick = false;
                                this.outValue.emit(false)
                            } else {
                                if (this.tableList2.length > 0) {
                                    let arr = [];
                                    this.tableList2.forEach(element => {
                                        arr.push({ indicatorId: this.bean.indicatorId, indicatorAttr: this.bean.indicatorEnName, indicatorAttrValue: element.indicatorAttrValue, description: element.description })
                                    });
                                    let param = {
                                        inputParams: {
                                            operType: '1',
                                            indicatorList: arr,
                                        }
                                    }
                                    this.cusPoolHttpService.modifyIndicatorEnum(param).subscribe(data => {
                                        if (data.returnCode.code == 'success') {
                                            this.isClick = false;
                                            this.outValue.emit(false)
                                        } else {
                                            this.isClick = false;
                                            this.msgs = [];
                                            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                                        }
                                    }, error => {
                                        this.isClick = false;
                                        this.msgs = [];
                                        this.msgs.push({ severity: 'error', summary: '', detail: "" });
                                    });
                                } else {
                                    this.isClick = false;
                                    this.outValue.emit(false)
                                }
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: "" });
                    });
                }
            } else {
                this.isClick = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.isClick = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    // 
    changeShowType(item) {
        if (item == '04') {
            this.vlaue = '1';
            if (this.tableList2.length == 0) {
                this.add();
            }
        } else {
            this.vlaue = '';
        }
    }

    // 
    add() {
        let aa = _.clone(this.tableList2);
        aa.push({
            fieldValue: '', describe: '',
        });
        this.tableList2 = aa;
    }

    // 
    delete(item, i) {
        if (item.sequenceId) {
            let param = {
                inputParams: {
                    sequenceId: item.sequenceId,
                }
            }
            this.cusPoolHttpService.deleteIndicatorEnum(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.queryCustPoolConfig();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: "" });
            });
        } else {
            this.tableList2.splice(i, 1);
            this.tableList2 = _.clone(this.tableList2);
        }
    }

    // 
    return() {
        this.outValue.emit(false)
    }
}