import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber, checkCard } from "app/@uisftech/common/service/validator";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import { ConfigPropertiesBean } from 'app/pages/tzb/custom/life-cycle/config-properties/bean/config-properties.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from "primeng/primeng";
import { ConfirmationService } from "primeng/primeng";

@Component({
    selector: 'config-properties',
    templateUrl: './config-properties.component.html',
    styleUrls: ['./config-properties.component.scss'],
    providers: [ConfirmationService]
})
export class ConfigPropertiesComponent implements OnInit {

    bean: ConfigPropertiesBean = new ConfigPropertiesBean();
    tableList: any = [];
    first: number = 0;
    totalNum: number = 0;
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    upInvalue: any;
    dataType: any = [];
    status: any = [];
    showType: any = [];
    managePermission: any;
    discustom: boolean;
    userform: FormGroup;
    loadingShow: any = { flag: false }; // loading
    code: any;
    dataList: any = [];
    riskList: any = [];
    idTypeList: any;//
    statusIdList: any;//
    inListTypeList: any;//
    display: boolean = false;//
    riskdisplay: boolean = false;//
    showModel: any;//
    addUpHeader: any;//
    inValue: any = [];/////
    modifyValue: any;////
    userId: string;
    key: boolean = false;//

    constructor(
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public cusPoolHttpService: CusPoolHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues() //
        this.dataType = this.code['PoolDataType'];
        this.status = this.code['PoolStatus'];
        this.showType = this.code['PoolShowType'];
        // this.dataType = [
        //     { label: '', value: 's' },
        //     { label: '', value: 'i' },
        //     { label: '', value: 'f' },
        //     { label: '', value: 'w' },
        //     { label: '', value: 'dt' },
        //     { label: '', value: 'dpi' },
        //     { label: '', value: 'dpf' },
        //     { label: '', value: 'dps' },
        // ]
        // this.status = [
        //     { label: '', value: '0' },
        //     { label: '', value: '1' },
        // ]
        // this.showType = [
        //     { label: '', value: '01' },
        //     { label: '', value: '04' },
        // ]
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.managePermission = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission
    }

    ngOnInit() {
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryInit();
    }

    // 
    queryInit() {
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
        this.query();
    }

    //
    query() {
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.queryCustPoolConfig(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    this.totalNum = data.outputParams.totalNum;
                } else {
                    this.tableList = [];
                    this.totalNum = 0;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    // 
    add() {

        if (this.tableList && this.tableList.length < 30) {
            this.display = true;
            this.showModel = '1';
            this.addUpHeader = '';
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'info', summary: '', detail: "30" });
        }
    }

    // 
    addCall(param) {
        this.display = param;
        this.showModel = '';
        this.queryInit();
    }

    // //
    modify(col, i) {
        this.upInvalue = col;
        this.display = true;
        this.showModel = i;
        if (i == '1') {
            this.addUpHeader = '';
        } else if (i == '3') {
            this.addUpHeader = '';
        } else if (i == '4') {
            this.addUpHeader = '';
        }
    }

    // 
    delete(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {
                    inputParams: { indicatorId: item.indicatorId, deleteMode: '0' }
                };
                this.cusPoolHttpService.deleteCustPoolConfig(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: '' });
                        this.queryInit()
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }

    // //
    updateCall(param) {
        this.display = param;
        this.showModel = '';
        this.queryInit();
    }
    // 
    detail(col, i) {
        this.display = true;
        this.inValue = col;
        this.showModel = 4;
        this.modifyValue = i;
        this.addUpHeader = '';
    }
    // 
    detailCall(param) {
        this.display = param;
    }
    //
    batch(col) {
        this.inValue = col;
        this.display = true;
        this.showModel = 3;
        if (col == '1') {
            this.addUpHeader = '';
        } else if (col == '2') {
            this.addUpHeader = '';
        }
    }

    // 
    batchCall(param) {
        this.display = param;
    }

    // 
    reset() {
        this.bean = new ConfigPropertiesBean();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
    }

    //
    paginate(event) {
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.query();
    }

    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }

    //
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

    close() {
        this.showModel = '';
        this.display = false;
    }
}